:root{
  --main-clr: #ffffff;
  --main-light: #e7e7e7;
  --secondary-clr: #5400ff;
  --secondary-light: #7633FF;
  --third-clr: #ffe100;
  --third-clr-intense: #89cd00;
  --third-clr-light:#ffc85b;
  --intense-clr: #35009e;
  --font-clr: #2c2c2c;
  --font-light: #6b6b6b;
  --border-clr: #dfdfdf;
  --success-clr: #49ff46;
  --fail-clr: #ff3333;
  --warning-clr: rgb(255, 255, 84);
  --radius: 10px;
  --blur-clr: rgba(132, 132, 132, 0.8);
  --material-shadow: 0 1px 3px rgba(0,0,0,0.12), 0 1px 2px rgba(0,0,0,0.24);
  --material-shadow-intense: 5px 5px 3px #ebebeb, -5px -5px 3px #ffffff;
}

*{
  box-sizing: border-box;
}

html{
  margin: 0;
  padding: 0;
  overflow-x: hidden;
}
body{
  margin: 0;
  padding: 0;
  width: 100%;
  min-height: 100vh;
  font-family: 'Noto Sans', sans-serif;
  overflow-x: hidden;
}
body p{
  margin: 0;
}
body textarea{
  font-family: 'Noto Sans', sans-serif
}


.app-wrapper {
  text-align: center;
}