.not-found{
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.not-found h1{
    font-size: 2rem;
}
.not-found p{
    font-size: 1.5rem;
}


.no-results{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 2em;
}
.no-results div {
    width: 200px;
    height: 200px;
    overflow: hidden;
}
.no-results img{
    width: 100%;
    height: 100%;
    object-fit: contain;
}
.no-results h1{
    font-size: 16px;
    color: var(--font-clr);
    width: 60%;
}
.no-results p{
    color: var(--font-clr);
    font-size: 0.9rem;
    width: 60%;
    line-height: 1.5em;
}