.profile-wrapper{
    position: relative;
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    padding-top: 10vh;
}


.profile-nav{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 10vh;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0em 1em;
    background: var(--main-clr);
    border-bottom: 1px solid var(--border-clr);
    z-index: 10;
}
.logo{
    text-decoration: none;
    display: flex;
    align-items: center;
    gap: 0.5em;
}
.logo img{
    width: 1em;
    height: 1em;
}
.logo h1{
    color: var(--secondary-clr);
    font-size: 1em;
    text-transform: uppercase;
    font-weight: 600;
}


.profile-content-wrapper{
    position: relative;
    width: 100%;
    height: 90vh;
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    background: var(--main-clr);
}


/* Profile dashboard */
.profile-dashboard{
    width: 80%;
    position: relative;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
}


.profile-dashboard-approval{
    position: relative;
    width: 80%;
    height: 80%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 1em;
}
.profile-dashboard-approval-img{
    width: 10em;
    height: 10em;
}
.profile-dashboard-approval-img img{
    width: 100%;
    height: 100%;
    object-fit: contain;
}

.profile-dashboard-approval p{
    font-size: 1.2rem;
    color: var(--font-clr);
}
.profile-dashboard-approval small{
    width: 50%;
    font-size: 0.8rem;
    color: var(--font-light);
}

/* Universal dashboard styles */
.profile-dashboard-wrappers{
    width: 100%;
    display: flex;
    flex-direction: column;
    padding: 1em;
    gap: 1em;
}

.profile-dashboard-title{
    font-size: 18px;
    color: var(--font-clr);
    text-align: left;
    margin: 0;
}