.connections-check-wrapper{
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 30;
    background: rgba(46, 43, 43, 0.825);
    display: flex;
    align-items: center;
    justify-content: center;
}

.connections-check{
    width: 50%;
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 1em;
    padding: 3em;
    background: var(--main-clr);
    box-shadow: var(--material-shadow);
    border-radius: var(--radius);
    overflow: hidden;
}
.connections-check h1{
    width: max-content;
    font-size: 1rem;
    font-weight: 600;
    color: var(--font-clr);
    margin: 0;
}
.connections-check p{
    color: var(--font-light);
    font-size: 0.9rem;
    line-height: 1.5em;
    margin: 0;
}
.connections-check-name{
    color: var(--secondary-clr);
    font-weight: 500;
}
.connection-check-btns{
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 0.5em;
    margin-top: 2em;
}
.connection-check-btns button{
    padding: 1em 2em;
    border-radius: 5px;
    border: none;
    background: var(--secondary-clr);
    color: var(--main-clr);
    cursor: pointer;
}
.connection-check-btns button:hover{
    background: var(--intense-clr);
}
.connection-check-btns button:nth-child(1){
    background: transparent;
    color: var(--font-clr);
}
.connection-check-btns button:nth-child(1):hover{
    color: var(--intense-clr);
}