.backlink-connections-preferences{
    position: relative;
    flex: 0 0 65%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1em;
    padding: 1em;
    overflow: auto;
}
.backlink-connections-preferences::-webkit-scrollbar {
    width: 10px;
}
.backlink-connections-preferences::-webkit-scrollbar-track {
    background: #f1f1f1;
}
.backlink-connections-preferences::-webkit-scrollbar-thumb {
    background: #888;
}
.backlink-connections-preferences::-webkit-scrollbar-thumb:hover {
    background: #555;
}