.register-wrapper{
    width: 100%;
    height: 100vh;
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    background: transparent;
    padding-top: 6em;
}
.register-wrapper h5{
    width: 70%;
    text-align: left;
    margin: 0;
    font-size: 2rem;
    color: var(--font-clr);
    font-weight: 300;
}
.register-form-p{
    text-align: left;
    width: 70%;
    font-size: 0.9rem;
    line-height: 1.5em;
    margin: 0;
    color: var(--font-light);
}


/* Register promo */
.register-promo{
    position: fixed;
    right: 0;
    bottom: 0;
    width: 50%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 2em;
    background: radial-gradient(circle, rgba(118,51,255,1) 44%, rgba(63,0,188,1) 86%);
    border-radius: 25px 0 0 25px;
    overflow: hidden;
}
.register-promo-card{
    width: 85%;
    height: 75%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 2em;
}
.register-promo p{
    text-align: center;
    color: white;
    line-height: 1.5em;
    width: 40%;
}
.register-promo img{
    width: 50%;
    height: 50%;
}
.register-promo-btns{
    pointer-events: none;
    display: flex;
    align-items: center;
}
.register-promo-btn{
    pointer-events: none;
    width: max-content;
    height: max-content;
    background: transparent;
    border: none;
    color: white;
    padding: 0;
    margin: 0;
}
.register-promo-btn-active{
    background: transparent;
    border: none;
    color: rgb(230, 230, 230);
    transform: scale(1.5);
    padding: 0;
    margin: 0;
}


/* Register form */
.register-form-wrapper{
    width: 50%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1em;
}
#register-form{
    width: 70%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 1em;
    margin: 2em 0;
}
.finish-registration-name{
    width: 100%;
    display: flex;
    align-items: center;
    gap: 0.5em;
}
.register-form-input{
    position: relative;
    width: 100%;
    display: flex;
    align-items: center;
    gap: 0.5em;
    border: 1px solid var(--border-clr);
    border-radius: 5px;
    padding: 0 1em;
}
.register-form-input:focus-within{
    border-bottom: 2px solid var(--secondary-clr);
}
.register-form-input input{
    width: 90%;
    border: none;
    padding: 1em;
    outline: none;
}
.register-icons{
    width: 5%;
    color: var(--font-clr);
}


.register-btn{
    width: 100%;
    background: var(--secondary-clr);
    border: none;
    border-radius: var(--radius);
    color: white;
    padding: 1em 2em;
    cursor: pointer;
    transition: background 0.3s;
    will-change: background;
}
.register-btn:hover{
    background: var(--intense-clr);
}
.register-btn-disabled{
    width: 100%;
    background: rgb(155, 155, 255);
    border: none;
    border-radius: var(--radius);
    color: white;
    padding: 1em 2em;
    pointer-events: none;
}


.register-form-wrapper span{
    font-size: 0.9rem;
    color: var(--font-light);
    text-align: left;
    width: 70%;
}
.register-login-btn{
    text-decoration: none;
    text-align: left;
}


/* Finish registration page */
.finish-registration{
    width: 100%;
    min-height: 100vh;
    display: flex;
    justify-content: flex-start;
    padding-top: 5em;
}
.finish-registration-content{
    background: white;
    width: 50%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 1em;
}
.finish-registration-content h5{
    width: 70%;
    text-align: left;
    margin: 0;
    font-size: 2rem;
    color: var(--font-clr);
    font-weight: 300;
}
.finish-registration-content p{
    width: 70%;
    text-align: left;
    font-size: 0.9rem;
    line-height: 1.5em;
    margin: 0;
    color: var(--font-light);
}


#finish-registration-form{
    width: 70%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 1em;
    margin: 3em 0;
}
#finish-registration-form small{
    text-align: left;
    color: grey;
}
.finish-registration-name{
    display: flex;
}
.finish-registration-form-input{
    position: relative;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 0.5em;
    padding: 0 1em;
    border: 1px solid var(--border-clr);
    border-radius: 5px;
}
.finish-registration-form-input:focus-within{
    border-bottom: 2px solid var(--secondary-clr);
}
.finish-registration-form-input input, select {
    width: 90%;
    background: transparent;
    border: none;
    padding: 1em 2em;
    outline: none;
}
.finish-registration-form-input select{
    padding: 1em 0;
}
.finish-registration-form-input option{
    font-size: 1rem;
}
.finish-form-icon{
    color: var(--font-clr);
}
.finish-registration-btn{
    margin-top: 1em;
    width: 100%;
    background: var(--secondary-clr);
    border: none;
    border-radius: var(--radius);
    color: white;
    padding: 1em 2em;
    cursor: pointer;
    transition: background 0.3s;
    will-change: background;
}
.finish-registration-btn:hover{
    background: var(--intense-clr);
}


/* Register await confiramtion page */
.register-await-confirmation{
    background: radial-gradient(circle, rgba(118,51,255,1) 44%, rgba(63,0,188,1) 86%);
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 1em;
}

.register-await-confirmation h1{
    color: var(--main-clr);
    text-align: left;
    width: 50%;
    font-weight: 400;
}

.register-await-confirmation p{
    width: 50%;
    color: var(--main-light);
    text-align: left;
    font-size: 1.5rem;
    line-height: 1.3em;
    font-weight: 300;
}
.register-await-confirmation-info{
    font-size: 1rem;
    width: 50%;
    color: rgb(201, 201, 201);
    text-align: left;
    text-transform: none;
}

.register-await-confirmation div{
    margin: 2em;
    width: 50%;
    display: flex;
    flex-direction: column;
}
.register-await-confirmation div small{
    text-align: left;
    font-size: 0.8rem;
    line-height: 1.5em;
    color: rgb(201, 201, 201);
    text-transform: uppercase;
}
.register-await-link{
    color: var(--third-clr);
    font-weight: 500;
    font-size: 1rem;
}

/* MEDIA QUERIES */
@media only screen and (max-width: 480px){
    .register-form-wrapper{
        width: 100%;
    }
    .register-wrapper h5{
        text-align: left;
        width: 80%;
    }
    .register-form-p{
        text-align: left;
        width: 80%;
    }
    .register-promo{
        display: none;
    }
    #register-form{
        width: 80%;
    }

    .finish-registration-content{
        width: 100%;
    }
    .finish-registration-content h5, .finish-registration-content p{
        width: 80%;
        text-align: left;
    }
    #finish-registration-form{
        width: 80%;
    }


    .register-await-confirmation h1, .register-await-confirmation p, .register-await-confirmation-info, .register-await-confirmation div{
        width: 80%;
    }
}