.profile-landing-premium{
    position: relative;
    width: 100%;
    border-radius: 15px;
    padding: 2em;
    background: var(--third-clr);
    box-shadow:  7px 7px 14px #e8e8e8,
    -7px -7px 14px #f4f4f4;
    display: flex;
    flex-direction: column;
    gap: 0.5em;
    overflow: hidden;
    margin-bottom: 1em;
}
.profile-landing-premium-shape:nth-child(1){
    width: 10%;
    height: 200%;
    background: var(--secondary-light);
    transform: rotate(45deg);
    position: absolute;
    z-index: 2;
    right: 10%;
    top: -50%;
}
.profile-landing-premium-shape:nth-child(2){
    width: 15%;
    height: 200%;
    background: var(--intense-clr);
    transform: rotate(45deg);
    position: absolute;
    right: 0%;
    top: -50%;
}

.profile-landing-premium img{
    position: absolute;
}
.profile-landing-premium h1{
    text-align: left;
    font-weight: 500;
    font-size: 1.3rem;
    color: var(--intense-clr);
    margin: 0;
}
.profile-landing-premium p{
    width: 70%;
    color: var(--secondary-clr);
    text-align: left;
    font-size: 0.8rem;
    margin: 0;
}
.profile-landing-premium small{
    color: var(--main-light);
    text-align: left;
    font-size: 0.8rem;
}
.profile-landing-premium-btns{
    margin-top: 1em;
    display: flex;
    align-items: center;
    gap: 1em;
}
.profile-landing-premium-btn{
    border: none;
    padding: 0.5em 1em;
    border-radius: 15px;
    font-size: 0.9rem;
    background: var(--secondary-clr);
    color: var(--main-clr);
    text-decoration: none;
    transition: all 0.3s;
    cursor: pointer;
}
.profile-landing-premium-btn:hover{
    background: var(--intense-clr);
}