.new-landing{
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-bottom: 2em;
}

.new-landing header{
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 2em;
    padding-top: 5em;
}
.new-landing header h1{
    font-size: 5rem;
    width: 60%;
    line-height: 1.1em;
    color: var(--font-clr);
    margin: 0;
}
.new-landing header h1 span{
    color: var(--secondary-clr);
}
.new-landing header p{
    width: 60%;
    color: var(--font-light);
    margin: 0;
}
.new-landing header button{
    background: var(--secondary-clr);
    border: none;
    border-radius: 5px;
    padding: 1em 2em;
    color: var(--main-clr);
}


.faqs-wrapper{
    width: 100%;
    height: max-content;
    display: flex;
    flex-direction: row;
    gap: 1em;
    padding: 2em;
}
.faq{
    width: 50%;
    height: 300px;
    background: var(--secondary-clr);
    border-radius: var(--radius);
    padding: 1em;
    display: flex;
    flex-direction: column;
    gap: 1em;
}
.faq h5{
    font-weight: 300;
    text-align: left;
    font-size: 1.2rem;
    color: var(--main-clr);
    margin: 0;
}
.faq p{
    text-align: left;
    color: var(--main-light);
    font-size: 0.8rem;
    margin: 0;
}
.faq button{
    background: var(--third-clr);
    border: none;
    border-radius: 5px;
    color: var(--main-clr);
    padding: 0.5em 1em;
}