.nav{
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    justify-content: flex-start;
    gap: 2em;
    align-items: center;
    padding: 1em 3em;
    transition: background 0.2s;
    z-index: 10;
}

.logo-wrapper{
    width: 6em;
}
.logo-wrapper h1{
    font-size: 1em;
    color: black;
}
.logo-wrapper img{
    width: 100%;
    height: 100%;
    object-fit: contain;
}

/* Nav menu links */
.nav-menu{
    display: flex;
    align-items: center;
    gap: 3em;
}
.nav-menu-links{
    position: relative;
    text-decoration: none;
    color: var(--font-clr);
    font-size: 0.9rem;
    transition: all 0.2s;
    transform-origin: top;
}
.nav-menu-links:hover{
    background: var(--secondary-clr);
    color: var(--main-clr);
    padding: 0.5em;
    border-radius: 5px;
}
.nav-menu-links:hover::after{
    transform: scaleX(1);
}
.nav-menu-links::after{
    content: "";
    position: absolute;
    left: 0;
    bottom: -10px;
    width: 100%;
    height: 1px;
    background: var(--secondary-clr);
    transform: scaleX(0);
    transform-origin: left;
    transition: all 0.2s;
}

.nav-account{
    position: absolute;
    right: 2em;
    display: flex;
    align-items: center;
    gap: 1em;
}
.signup-link{
    text-decoration: none;
    background: transparent;
    border: none;
    border-radius: 25px;
    color: var(--font-clr);
    font-size: 1rem;
    cursor: pointer;
    transition: color 0.3s;
    will-change: color;
}
.signup-link:hover{
    color: var(--secondary-clr);
}
.login-link{
    text-decoration: none;
    background: var(--secondary-clr);
    color: var(--main-clr);
    font-size: 1rem;
    border: none;
    border-radius: 5px;
    padding: 0.5em 1em;
    cursor: pointer;
    transition: background 0.3s;
    will-change: background;
}
.login-link:hover{
    background: var(--third-clr);
}


/* Nav user */
.nav-user{
    position: absolute;
    right: 3em;
    display: flex;
    align-items: center;
    gap: 0.5em;
}
.nav-user p{
    font-size: 0.9rem;
    color: var(--font-clr);
}
.nav-user span{
    color: var(--font-clr);
    font-weight: 500;
}
.nav-user-notifications{
    position: relative;
    background: transparent;
    border: none;
    font-size: 1.1rem;
    display: flex;
    justify-content: center;
    align-items: center;
}
.nav-user-notifications-btn{
    position: relative;
    background: transparent;
    border: none;
    cursor: pointer;
    font-size: 1.2rem;
    color: var(--font-clr);
    display: flex;
    align-items: center;
    justify-content: center;
}
.nav-user-notifications-count{
    position: absolute;
    top: -5px;
    right: -2px;
    width: 19px;
    height: 19px;
    border-radius: 50%;
    background: #CC0000;
    font-size: 0.7rem;
    color: var(--main-clr);
    border: 2px solid var(--main-clr);
    display: flex;
    align-items: center;
    justify-content: center;
}


.nav-user-profile-wrapper{
    position: relative;
    display: flex;
    flex-direction: column;
}
.nav-user-profile{
    width: 2em;
    height: 2em;
    border-radius: 50%;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid var(--border-clr);
    background: var(--main-clr);
}
.nav-user-options{
    position: absolute;
    right: 0;
    bottom: -290px;
    width: 250px;
    border-radius: var(--radius);
    border: 1px solid var(--border-clr);
    background: var(--main-clr);
    box-shadow: var(--material-shadow);
    display: flex;
    flex-direction: column;
    gap: 0.5em;
    overflow: hidden;
    z-index: 10;
}


.nav-options-top{
    height: 70px;
    display: flex;
    align-items: center;
    gap: 1em;
    padding: 1em;
    border-top: 1px solid  var(--border-clr);
}
.nav-options-top-img{
    width: 2em;
    height: 2em;
    border-radius: 50%;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid var(--border-clr);
}
.nav-user-options p{
    text-align: left;
    margin: 0;
    color: var(--font-clr);
    font-weight: 600;
}
.nav-user-options small{
    text-align: left;
    margin: 0;
    font-size: 0.8rem;
    color: var(--font-light);
}


.nav-options-btns{
    height: 200px;
    display: flex;
    flex-direction: column;
    gap: 0.5em;
    overflow: auto;
}
.nav-options-btns::-webkit-scrollbar {
    width: 10px;
}
.nav-options-btns::-webkit-scrollbar-track {
    background: #f1f1f1;
}
.nav-options-btns::-webkit-scrollbar-thumb {
    background: #888;
}
.nav-options-btns::-webkit-scrollbar-thumb:hover {
    background: #555;
}
.nav-options-btn{
    text-decoration: none;
    background: transparent;
    width: 100%;
    height: 25%;
    border: none;
    border-bottom: 1px solid var(--border-clr);
    display: flex;
    align-items: center;
    gap: 0.5em;
    padding: 0.5em 1em;
    font-size: 1rem;
    color: var(--font-clr);
    cursor: pointer;
    transition: background 0.3s;
    will-change: background;
}
.nav-options-btn:hover{
    background: var(--main-light);
}


@media only screen and (max-width: 480px){
    .nav{
        padding: 0.5em;
    }
    .nav-menu-links{
        display: none;
    }
}