.landing-wrapper{
    position: relative;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}


/* Header */
.header-wrapper{
    position: relative;
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    padding: 10em 7em 0 5em;
}


.header-video {
    position: absolute;
    right: -2em;
    bottom: -25%;
    width: 35em;
    height: 35em;
    border-radius: 50%;
    overflow: hidden;
}
.header-video-small{
    position: absolute;
    right: -2em;
    top: 10%;
    width: 20em;
    height: 20em;
    border-radius: 50%;
    overflow: hidden;
}
.header-video video, .header-video-small video{
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.header-join{
    width: max-content;
    height: 3em;
    border-radius: 25px;
    border: 1px solid grey;
    display: flex;
    align-items: center;
}
.header-join input{
    background: transparent;
    border: none;
    padding: 1em 2em;
}
.header-join button{
    border: none;
    background: var(--third-clr);
    border-radius: 25px;
    font-size: 1.1rem;
    color: var(--secondary-clr);
    padding: 1em 2em;
    display: flex;
    align-items: center;
    justify-content: center;
}


.header-text{
    width: 80%;
    display: flex;
    flex-direction: column;
    gap: 1em;
}
.header-text h1{
    position: relative;
    font-weight: 900;
    text-transform: uppercase;
    font-family: 'Martel Sans', sans-serif;
    text-align: left;
    font-size: 6.5rem;
    color: var(--font-clr);
    line-height: 0.9em;
    margin: 0;
    mix-blend-mode: screen;
}
#header-text-span{
    position: relative;
    font-style: italic;
    color: var(--main-clr);
}
.header-text p{
    width: 70%;
    text-align: left;
    color: var(--font-light);
    line-height: 1.5em;
    font-size: 0.9rem;
    margin: 0;
}
.header-text p::before{
    content: "";
    position: absolute;
    top: -60px;
    right: 240px;
    width: 250px;
    height: 250px;
    border-radius: 50%;
    background: var(--third-clr);
    z-index: -1;
}

.sign-up-btn{
    text-decoration: none;
    width: max-content;
    display: flex;
    background: var(--third-clr);
    color: var(--secondary-clr);
    border-radius: var(--radius);
    padding: 1em 2em;
}

.header-fallback{
    width: 100%;
    height: 5vh;
    background: var(--secondary-light);
}


/* Info  */
.landing-info-wrapper{
    width: 100%;
    min-height: 100vh;
    margin: 15em 0;
    display: flex;
    flex-flow: row wrap;
    justify-content: center;
    gap: 2em;
}
.landing-info{
    background: var(--main-clr);
    box-shadow:  6px 6px 12px #e8e8e8,
    -6px -6px 12px #e8e8e8;
    width: 25%;
    min-height: 350px;
    border-radius: var(--radius);
    padding: 2em;
    margin-bottom: 2em;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    gap: 2em;
    transition: all 0.3s;
}
.landing-info:hover{
    transform: scale(1.1);
    background: rgb(238, 238, 238);
}
.landing-info-icon{
    font-size: 3rem;
    color: var(--secondary-light);
}
.landing-info h1{
    font-size: 2rem;
    width: 60%;
    font-weight: 400;
    text-align: left;
    color: var(--font-clr);
    margin: 0;
}
.landing-info p{
    text-align: left;
    color: var(--font-light);
    margin: 0;
    line-height: 1.5em;
}


/* Section-1 */
.section-1{
    position: relative;
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 3em;
    margin-top: 20em;
    background: var(--font-light);
    padding: 6em 4em;
    padding-bottom: 0;
    overflow: visible;
    background:linear-gradient(0deg, rgba(80, 80, 80, 0.95), rgba(88, 88, 88, 0.95)), url('../../resources/samples/22.jpg');
    /* clip-path: polygon(0 0, 100% 8%, 100% 100%, 0% 100%); */
}
.section-1-img{
    width: 80%;
    height: 100vh;
    position: absolute;
    z-index: 3;
    bottom: -55%;
    border-radius: 25px;
    padding: 2em;
    overflow: hidden;
    background: var(--main-clr);
}
.section-1 img{
    width: 100%;
    height: 100%;
    object-fit: contain;
    border-radius: 25px;
}
.section-1 h1{
    margin: 0;
    font-size: 0.8rem;
    font-weight: 600;
    color: white;
    border: 1px solid white;
    border-radius: 25px;
    padding: 0.5em 1em;
}
.section-1 p{
    width: 70%;
    margin: 0;
    font-size: 1.2rem;
    text-transform: uppercase;
    color: white;
}
.section-1 button{
    background: orange;
    border: none;
    border-radius: var(--radius);
    padding: 1em 2em;
    color: white;
    font-size: 1.2rem;
}


/* Section-2 */
.section-two{
    width: 100%;
    height: max-content;
    display: flex;
    justify-content: space-between;
    margin-top: 40%;
}

.section-two-img{
    width: 40%;
    height: 100%;
    border: 2px solid rgb(214, 214, 214);
    border-radius: 25px;
    transform: rotate(45deg);
    overflow: hidden;
}
.section-two-img img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    transform: rotate(-45deg);
}
.section-two-content{
    width: 50%;
    padding: 5em;
    display: flex;
    flex-direction: column;
    gap: 2em;
}
.section-two-content h1{
    text-align: left;
    line-height: 0.9em;
    font-size: 2rem;
    color: var(--font-clr);
    margin: 0;
}
.section-two-content p{
    text-align: left;
    color: var(--font-clr);
    margin: 0;
    line-height: 1.5em;
}
.section-two-content button{
    padding: 1em 2em;
    background: var(--main-clr);
    border: 1px solid var(--secondary-clr);
    color: var(--secondary-clr);
    border-radius: 25px;
    width: max-content;
}

/* section 3 */
.section-three{
    width: 100%;
    height: 150vh;
    background: var(--third-clr);
}
.section-three-top{
    width: 100%;
    height: 50%;
    background: var(--secondary-clr);
    clip-path: polygon(0 0, 100% 0, 100% 85%, 0% 100%);
}
.section-three-bottom{
    width: 100%;
    height: 50%;
    background: var(--main-clr);
    clip-path: polygon(0 15%, 100% 0, 100% 100%, 0% 100%);
}


/* MEDIA QUERIES */
@media only screen and (max-width: 980px){
    .login-img{
        display: none;
    }
    .header-wrapper{
        flex-direction: column-reverse;
        align-items: center;
        padding: 5em 0;
    }
    .header-text{
        width: 90%;
    }


    .landing-info{
        width: 90%;
    }


    .section-1 p{
        text-align: center;
        font-size: 1.5rem;
    }
    .section-1 img{
        top: -15%;
    }


    .section-two-wrappers{
        flex-direction: column;
    }
    .section-two-wrappers:nth-child(2){
        flex-direction: column-reverse;
    }
    .section-two-content{
        width: 100%;
        padding: 1em;
    }
    .section-two-wrappers img{
        width: 70%;
        height: 70%;
    }
}

@media only screen and (max-width: 480px){
    .header-wrapper{
        flex-direction: column-reverse;
        align-items: center;
        padding: 5em 0;
    }
    .header-text{
        width: 90%;
    }
    .header-text h1{
        font-size: 3rem;
    }
    .header-text p{
        width: 90%;
    }
    .header-text p::before{
        right: 0;
    }
    .header-video{
        display: none;
    }
}