.subscription-complete-progress{
    position: relative;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.subscription-complete-progress-bar{
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    top: 6px;
    width: 92%;
    height: 3px;
    background: var(--secondary-clr);
    transform-origin: left;
}

.subscription-complete-progress-checkmark{
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.3em;
}
.subscription-complete-progress-checkmark-step, .subscription-complete-progress-checkmark-step-active{
    width: 2em;
    height: 2em;
    border: 1px solid var(--secondary-clr);
    background: var(--main-clr);
    box-shadow: var(--material-shadow);
    color: var(--secondary-clr);
    font-size: 0.8rem;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
}
.subscription-complete-progress-checkmark-step-active{
    background: var(--secondary-clr);
    color: var(--main-clr);
}
.subscription-complete-progress-checkmark p{
    color: var(--secondary-clr);
    font-size: 0.8rem;
}