.services{
    width: 100%;
    background:linear-gradient(0deg, rgba(255, 255, 255, 0.8), rgba(255, 255, 255, 0.8)), url('../../../resources//samples/phone22.jpg');
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    padding-top: 10em;
}
.services-top{
    width: 70%;
}
.services-top h1{
    font-size: 7rem;
    color: var(--font-clr);
    margin: 0;
}
.services-top p{
    color: var(--font-light);
    font-size: 1.2rem;
    text-align: center;
    margin: 0;
}

.services-single-wrapper{
    margin-top: 15em;
    display: flex;
}
.services-single{
    width: 20%;
    height: 400px;
    padding: 2em;
    border-radius: 15px 15px 0 0;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    box-shadow: var(--material-shadow);
    border: 1px solid var(--border-clr);
    gap: 2em;
    transition: background 0.3s;
}
.services-single h1{
    color: var(--font-clr);
    font-weight: 900;
    font-size: 1.2rem;
    text-align: left;
    line-height: 1em;
    margin: 0;
}
.services-single > div{
    display: flex;
    flex-direction: column;
    gap: 1em;
}
.services-single p{
    font-size: 0.8rem;
    line-height: 1.5em;
    color: var(--font-light);
    text-align: left;
    margin: 0;
}
.services-single button{
    background: transparent;
    border: 1px solid var(--font-clr);
    border-radius: 25px;
    color: var(--font-clr);
    width: max-content;
    padding: 0.5em 1em;
}
